.product_item {
  padding-bottom: 10px;
  border-radius: 15px;
  display: grid;
  grid-template-rows: auto repeat(2, 1fr);
  gap: 10px;
  color: black;
}
.picture {
  display: flex;
  align-items: center;
}
.product_item img {
  width: 100%;
  height: 300px;
  object-fit: cover;
  border-radius: 10px;
}
.prices_blok {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
}
.disc_price,
.no_disc_price {
  font-weight: 600;
  font-size: 30px;
}
.no_disc_price {
  padding: 0 10px;
}
.price {
  font-weight: 500;
  font-size: 20px;
  text-decoration-line: line-through;
  color: #8b8b8b;
}
.percent {
  font-weight: 600;
  font-size: 20px;
  color: #ff32a1;
}
.product_name {
  font-weight: 500;
  font-size: 16px;
  padding-left: 10px;
}
.small_text {
  font-size: 22px;
}

.picture {
  display: flex;
  flex-direction: column;
  position: relative;
}
.add_btn {
  width: 100%;
  background: #f1fff1;
  border: 2px solid #339933;
  border-radius: 21px;
  padding: 25px;
  font-weight: 600;
  font-size: 18px;
  color: #339933;
  cursor: pointer;
  position: absolute;
  bottom: 5%;
  visibility: hidden;
}

.add_btn:hover {
  color: white;
  background: #339933;
}

.product_item:hover .add_btn {
  opacity: 1; 
   visibility: visible;
}
