.product_item {
  display: grid;
  grid-template-columns: 190px minmax(150px, 2fr) minmax(150px, 1fr) auto;
  grid-template-rows: 160px;
  gap: 25px;
  border-bottom: 1px solid gray;
  padding: 40px 0;
}
.product_item:first-child {
  border-top: 1px solid gray;
}
.product_item button {
  border: none;
  outline: none;
  background: transparent;
  cursor: pointer;
}
.product_img {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  object-fit: cover;
}
.grid_item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.product_name {
  font-weight: 600;
  font-size: 18px;
  color: #3a3a3a;
}
.btns_blok {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 60%;
  border: 1px solid #000000;
  border-radius: 12px;
  font-weight: 600;
  font-size: 18px;
}
.btn {
  font-size: 40px;
}
.prices {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-self: center;
}
.final_price {
  font-weight: 600;
  font-size: 40px;
}
.small_text {
  font-size: 22px;
}
.price {
  font-weight: 500;
  font-size: 20px;
  text-decoration-line: line-through;
  color: #8b8b8b;
}
.del_btn {
  align-self: flex-start;
}
